<template>
  <div id="app">
    <div class="banner">
      <img width="100%" src="http://i1.fuimg.com/657955/802edd68721fe003.png" />
    </div>
    <div
      id="index"
      style="height: 100%;">
      <component
        :is="currentView"
      />
    </div>
  </div>
</template>

<script>
import Add from './components/Add'

export default {
  name: 'App',
  components: {
    Add
  },
  data () {
    return {
      currentView: 'Add',
    }
  }
}
</script>

<style>
body{
  margin: 0;
  padding: 0;
  font-size: 1.4rem;
}
#app {
  font-family: SimHei,'黑体',Microsoft YaHei,微软雅黑,MicrosoftJhengHei,华文细黑,STHeiti,MingLiu;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #333;
}
.center{
  text-align: center
}
</style>



